

























































export default {
	data() {
		return {
			disabled:true,
			money:'',
			bank_code:'',
			loading: false,
			cashFee:'',
			yes_money:'',
		}
	},
	onLoad() {this.getCashData();},
	onShow() {},
	onShareAppMessage() {
		return {
			title: this.webshareTitle,
			path: '/pages/tabBar/index/index'
		}
	},
	computed: {},
	methods: {
		addCash(){
				const that = this;
				const openid = this.$store.state.openid;
				const sessionKey = this.$store.state.sessionKey;
				const money = this.money;
				if(this.money==''){
					uni.showModal({
						content: '请输入提现金额',
						showCancel: false
					});
					return;
				}
				
				try {
					if(this.loading){
						return;
					}
					this.loading = true;
					
					this.$http.post(this.websiteUrl + '/api/agent/addCash', {money,openid,sessionKey}, (res) => {
						if(res.data.code==1){
							uni.showModal({
								content:res.data.msg,
								showCancel:false,
								success:function() {
									that.loading = false;
									uni.navigateBack();
								}
							})
						}else{
							uni.showModal({
								content:res.data.msg,
								showCancel:false,
								success:function() {
									that.loading = false;
								}
							})
						}
						
					});
				} catch (e) {
					uni.showModal({
						content: '网络异常,请稍后重试...',
						showCancel: false,
						success:function() {
							that.loading = false;
						}
					});
				}
				
				
		},
		getCashData(){
			try {
				const openid = this.$store.state.openid;
				const sessionKey = this.$store.state.sessionKey;
				this.$http.post(this.websiteUrl + '/api/agent/getCashData', {openid,sessionKey
				}, (res) => {
					if (res.data.code == 1) {
						this.bank_code = res.data.result.agent.bank_name+'(尾号'+res.data.result.agent.bank_code+')';
						this.cashFee =  res.data.result.agent.cashFee;
						this.yes_money = res.data.result.agent.yes_money;
						if(res.data.result.agent.bank_code!=''){
							this.disabled =false;
						}
					}else{
						uni.showModal({
							content: res.data.msg,
							showCancel: false
						})
						
					}
				});
			} catch (e) {
				uni.showModal({
					content: '网络异常,请稍后重试...',
					showCancel: false
				})
			}
		}

	}

}
